import { income_tax_sum } from "./incometax-set.cul?cul_scope_id=1&cul_parent_scope_id=0";import { gross_salary } from "./incometax-set.cul?cul_scope_id=1&cul_parent_scope_id=0";import { taxpayer_table } from "./incometax-set.cul?cul_scope_id=1&cul_parent_scope_id=0";import { taxpayer_id } from "./incometax-set.cul?cul_scope_id=1&cul_parent_scope_id=0";import { income_tax_impact } from "./impact.cul.js";import { income_tax_sum_impact } from "./impact.cul.js";import { effective_rate_proposed } from "./proposed.cul?cul_scope_id=2&cul_parent_scope_id=0";import { income_tax_proposed } from "./proposed.cul?cul_scope_id=2&cul_parent_scope_id=0";import { income_tax_sum_proposed } from "./proposed.cul?cul_scope_id=2&cul_parent_scope_id=0"; // a simple one

export const net_salary = ({ taxpayer_table_in, taxpayer_id_in }) => gross_salary({ taxpayer_table_in, taxpayer_id_in }) - income_tax({ taxpayer_table_in, taxpayer_id_in });

export const gross_salary_ = ({ gross_salary_in }) => gross_salary_in;

export const income_tax = ({ taxpayer_table_in, taxpayer_id_in }) => Math.max(paye({ taxpayer_table_in, taxpayer_id_in }) + prsi({ taxpayer_table_in, taxpayer_id_in }) + usc({ taxpayer_table_in, taxpayer_id_in }) - tax_credit({}), 0);

export const tax_credit = ({}) => 1000;

export const effective_rate = ({ taxpayer_table_in, taxpayer_id_in }) => 1 - net_salary({ taxpayer_table_in, taxpayer_id_in }) / gross_salary({ taxpayer_table_in, taxpayer_id_in });

export const prsi = ({ taxpayer_table_in, taxpayer_id_in }) => gross_salary({ taxpayer_table_in, taxpayer_id_in }) * 0.04;

export const band = ({}) => 30000;

export const paye = ({ taxpayer_table_in, taxpayer_id_in }) => {
  return 0.2 * gross_salary({ taxpayer_table_in, taxpayer_id_in }) + 0.2 * Math.max(gross_salary({ taxpayer_table_in, taxpayer_id_in }) - band({}), 0);
};

export const usc_table = ({}) => [
{ band_id: 1, band_co: 12012, rate: 0.005 },
{ band_id: 2, band_co: 21295, rate: 0.02 },
{
  band_id: 3,
  band_co: 70144,
  rate: 0.045 },

{ band_id: 4, band_co: 0, rate: 0.08 }];


export const band_id = ({ band_id_in }) => band_id_in;

export const usc_band_end = ({ band_id_in }) => {
  if (band_id({ band_id_in }) == usc_table({}).length) return 999999999;
  return usc_table({})[band_id({ band_id_in }) - 1];
};
export const usc_band_start = ({ band_id_in }) => {
  if (band_id({ band_id_in }) == 1) return 0;
  return usc_table({})[band_id({ band_id_in }) - 2];
};
export const usc_rate = ({ band_id_in }) => usc_table({})[band_id({ band_id_in }) - 1];
export const usc_by_band_id = ({ band_id_in, taxpayer_table_in, taxpayer_id_in }) =>
usc_rate({ band_id_in }) *
Math.min(
usc_band_end({ band_id_in }) - usc_band_start({ band_id_in }),
Math.max(gross_salary({ taxpayer_table_in, taxpayer_id_in }) - usc_band_start({ band_id_in }), 0));

export const usc = ({ taxpayer_table_in, taxpayer_id_in }) => usc_table({}).reduce((a, v) => a + usc_by_band_id({ taxpayer_table_in, taxpayer_id_in, band_id_in: v.band_id }), 0);
